import React from 'react';
import { Users, ShieldAlert, Activity, UserPlus } from 'lucide-react';

export default function DashboardPage() {
    const stats = [
        { label: "Total Users", value: "1,284", icon: <Users className="text-amber-500" />, change: "+12%" },
        { label: "Security Logs", value: "45", icon: <ShieldAlert className="text-red-500" />, change: "Last 24h" },
        { label: "System Uptime", value: "99.9%", icon: <Activity className="text-emerald-500" />, change: "Stable" },
        { label: "New Requests", value: "7", icon: <UserPlus className="text-blue-500" />, change: "Pending" },
    ];

    return (
        <div className="space-y-8">
            {/* Header */}
            <div>
                <h1 className="text-3xl font-bold text-white font-serif">System Overview</h1>
                <p className="text-gray-400">Real-time status of your user management portal.</p>
            </div>

            {/* Stats Grid */}
            <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6">
                {stats.map((stat, index) => (
                    <div key={index} className="bg-white/5 border border-white/10 p-6 rounded-2xl hover:border-amber-500/50 transition-colors group">
                        <div className="flex justify-between items-start mb-4">
                            <div className="bg-white/5 p-3 rounded-xl group-hover:bg-amber-500/10 transition-colors">
                                {stat.icon}
                            </div>
                            <span className="text-xs font-mono text-gray-500">{stat.change}</span>
                        </div>
                        <p className="text-gray-400 text-sm">{stat.label}</p>
                        <h3 className="text-2xl font-bold text-white mt-1">{stat.value}</h3>
                    </div>
                ))}
            </div>

            {/* Main Content Sections */}
            <div className="grid grid-cols-1 lg:grid-cols-3 gap-8">
                {/* Left Column: Recent Activity */}
                <div className="lg:col-span-2 bg-white/5 border border-white/10 rounded-2xl p-6">
                    <h2 className="text-xl font-bold text-white mb-6">Recent Audit Logs</h2>
                    <div className="space-y-4">
                        {[1, 2, 3, 4].map((i) => (
                            <div key={i} className="flex items-center justify-between p-4 bg-white/5 rounded-xl border border-white/5">
                                <div className="flex items-center gap-4">
                                    <div className="w-2 h-2 rounded-full bg-amber-500 shadow-[0_0_8px_rgba(245,158,11,0.5)]" />
                                    <div>
                                        <p className="text-sm text-white font-medium">User Role Modified</p>
                                        <p className="text-xs text-gray-500">Admin changed 'nikkita@gmail.com' to Editor</p>
                                    </div>
                                </div>
                                <span className="text-xs text-gray-600 font-mono">14:20 PM</span>
                            </div>
                        ))}
                    </div>
                </div>

                {/* Right Column: Quick Actions */}
                <div className="bg-amber-600/10 border border-amber-600/20 rounded-2xl p-6 h-fit">
                    <h2 className="text-xl font-bold text-amber-500 mb-4">Quick Actions</h2>
                    <div className="flex flex-col gap-3">
                        <button className="btn btn-sm bg-amber-600 hover:bg-amber-500 border-none text-white">Create New User</button>
                        <button className="btn btn-sm btn-outline border-white/10 text-white hover:bg-white/5">Export CSV Report</button>
                        <button className="btn btn-sm btn-outline border-white/10 text-white hover:bg-white/5">Database Backup</button>
                    </div>
                </div>
            </div>
        </div>
    );
}